import React from 'react';
import './Canvas.css';
import Card from '@material-ui/core/Card';

function Canvas(props) {
  const { classes } = props;
  return (
    <div className="canvas">
      <h2>Матрица лидерства Адизеса</h2>

      <div className="description">
        Основная задача менеджера - это сделать компанию эффективной и
        результативной в краткосрочной и долгосрочной перспектив
      </div>
      <Card>
        <div className="wrapper">
          <div className="title">
            <div className="vertical">Результат</div>
          </div>

          <div className="border">1</div>
          <div className="border">2</div>

          <div className="title">
            <div className="vertical">Процесс</div>
          </div>
          <div className="border">7</div>
          <div className="border">8</div>

          <div className="horizont-title horizont-title-2">
            Краткосрочная перспектива
          </div>
          <div className="horizont-title">Долгосрочная перспектива</div>
        </div>
      </Card>
      <h3>Описание</h3>

      <h3>Ключевые слова</h3>
    </div>
  );
}

export default Canvas;
